@import '../variables.scss';
@import '../mixins.scss';

form {
  display: flex;
  flex-direction: column;
}
.comment-area {
  background-color: #f2f2f2;
  margin: 0 -20px;
  margin-top: 22px;
  padding: 0 21px 34px 21px;
}

.comment-input {
  min-width: 320px;
  min-height: 63px;
  resize: none;
  margin: 23px 0;
  border: 1px solid #000000;
  border-radius: 1px;
}

.submit {
  @include fontStyle(bold, 16px, 21px);
  max-width: 281px;
  background-color: #fdd639;
  border-radius: 23px;
  padding: 13px 43px;
  border: 0;
  margin: 0 auto;
  cursor: pointer;
}
